import ComicsListItem from '../comicsListItem/ComicsListItem';

import Error from '../error/Error';
import Spinner from '../spinner/Spinner';
import useMarvelService from '../../services/MarvelService';
import './comicsList.scss';
import { useEffect, useState, useRef } from 'react';

const ComicsList = () => {
    const {getAllComics, _comicsOffset, loading, error} = useMarvelService();
    const [comicsList, setComicsList] = useState([]);
    const [newItemLoading, setNewItemLoading] = useState(false);
    const [comicsOffset, setComicsOffset] = useState(0);
    const [comicsEnded, setComicsEnded] = useState(false);
    
    useEffect(() => {
        setComicsOffset(_comicsOffset);
        onRequest(_comicsOffset, true);
    }, [])

    const onRequest = (offset, initial) => {
        initial ? setNewItemLoading(false) : setNewItemLoading(true);
        getAllComics(offset).then(onComicsListLoaded);
    }

    const onComicsListLoaded = (newComicsList) => {
        let ended = false;
        if(newComicsList.length < 8) {
            ended = true;
        }        
        setComicsList(comicsList => [...comicsList,...newComicsList]);
        setNewItemLoading(false);
        setComicsOffset(comicsOffset => comicsOffset + 9);
        setComicsEnded(ended);
    }

    const refsItem = useRef([]);

    const focusOnItem = (id) => {
        refsItem.current.forEach(item => item.classList.remove('active'));
        refsItem.current[id].classList.add('active');
        refsItem.current[id].focus();
    }

    const renderItems = (arr) => {
        let renderedIds = [];

        const items = arr.map(elem => {
            if (arr.length === 0) return null;
            if (renderedIds.includes(elem.id)) return null;
            renderedIds.push(elem.id);

                return (
                    <section className="comics__item" key={elem.id} ref={refsItem}>
                        <div className="item-img">
                            <img src={elem.thumbnail} alt={elem.title}/>
                        </div>
                        <div className="item-text">
                            <h2 className="title">{elem.title}</h2>
                            <span className="price">{elem.price}</span>
                        </div>
                    </section> 
            )
        })
            return (
                <>
                {items}
                </>
            )
    }
    
    const items = renderItems(comicsList);

    const errorMsg = error ? <Error/> : null;
    const spinner = loading && !newItemLoading ? <Spinner/> : null;

    return (
        <section className="comics-store">
                {errorMsg}
                {spinner}
                {items}
                {/* <ComicsListItem img={comicImg_1}/>
                <ComicsListItem img={comicImg_2}/>
                <ComicsListItem img={comicImg_1}/>
                <ComicsListItem img={comicImg_2}/>             */}
                 <button className="comics-btn"
                    disabled={newItemLoading}
                    style={{'display': comicsEnded ? 'none' : 'block'}}
                    onClick={() => onRequest(comicsOffset)}>Load more</button>
        </section>
    )
}

export default ComicsList;