// export default CharacterList;

const CharacterList = (props) => {
    const [charList, setCharList] = useState([]);
    const [newItemLoading, setNewItemLoading] = useState(false);
    const [offset, setOffset] = useState(0);
    const [charEnded, setCharEnded] = useState(false);
    const {loading, error, getAllChars, _baseOffset} = useMarvelService();

    useEffect(() => {
        setOffset(_baseOffset);
        onRequest(_baseOffset, true);
        // eslint-disable-next-line
    }, [])

    const onRequest = (offset, initial) => {
        initial ? setNewItemLoading(false) : setNewItemLoading(true);
        getAllChars(offset).then(onCharListLoaded);
    }

    const onCharListLoaded = (newCharList) => {
        let ended = false;
        if(newCharList.length < 9) {
            ended = true;
        }        
        setCharList(charList => [...charList,...newCharList]);
        setNewItemLoading(false);
        setOffset(offset => offset + 9);
        setCharEnded(charEnded => ended);
        }

    const refsItem = useRef([]);

    const focusOnItem = (id) => {
        refsItem.current.forEach(item => item.classList.remove('active'));
        refsItem.current[id].classList.add('active');
        refsItem.current[id].focus();
    }

    const renderItems = (arr) => {
        let renderedIds = [];
            const items = arr.map((elem, i) => {
                if (arr.length === 0) return null;
                if (renderedIds.includes(elem.id)) return null;
                renderedIds.push(elem.id);
                return (
                    <div 
                        className="characters-list__item" 
                        key={elem.id}
                        tabIndex={0}
                        ref={elem => refsItem.current[i] = elem}
                        onClick={(e) => {
                            props.onCharSelected(elem.id, e);
                            focusOnItem(i)
                            }
                        }
                        onKeyDown={(e) => {
                            if (e.key === ' ' || e.key === "Enter") {
                                props.onCharSelected(elem.id, e);
                                focusOnItem(i);
                            }
                        }}>
                        <div className="characters-list__item_img">
                            {elem.thumbnail.match(/not_available/g)
                                ? <img src={elem.thumbnail} alt="character" style={{objectFit: 'contain'}}/>
                                : <img src={elem.thumbnail} alt="character"/>}
                        </div>
                        <h2 className="char-name char-name_white">{elem.name}</h2>
                    </div>
                )
            });
            
            return (
                <>
                    {items}
                </>
            )
        };

        const items = renderItems(charList);

        const errorMsg = error ? <Error/> : null;
        const spinner = loading && !newItemLoading ? <Spinner/> : null;
        
        return (
                <section className="characters-list">
                    {errorMsg}
                    {spinner}
                    {items}
                    <button className="more homepage"
                    disabled={newItemLoading}
                    style={{'display': charEnded ? 'none' : 'block'}}
                    onClick={() => onRequest(offset)}>Load more</button>
                </section>
        )
}

CharacterList.propTypes = {
    onCharSelected: PropTypes.func
}

export default CharacterList;
