import {useState, useEffect} from 'react';

import useMarvelService from '../../services/MarvelService';
import Spinner from '../spinner/Spinner';
import Error from '../error/Error';

import './character.scss';

const Character = () => {
    const [char, setChar] = useState(null);
    const {loading, error, getChar, clearError} = useMarvelService();

    useEffect(() => {
        updateChar();
    }, []);

    const updateChar = () => {
        clearError();
        const id = Math.floor(Math.random() * (1011400 - 1011000) + 1011000);
        getChar(id).then(onCharLoaded).catch(error); 
    }

    const onCharLoaded = (char) => {
        setChar(char);
    }
    
    const HeroInfo = ({char}) => {
        const {name, desc, thumbnail, homepage, wikipage} = char;

        return (
            <div className="character-info">
                <div className="character-info__img">
                    {thumbnail.match(/not_available/g)
                    ? <img src={thumbnail} alt="character" style={{ objectFit: 'contain' }} />
                    : <img src={thumbnail} alt="character" />}
                    
                </div>
                <div className="character-info__text">
                    <h2 className="char-name">{name}</h2>
                    <p className="char-desc">{desc.length > 180 ? `${desc.substring(0, 179)}...` : desc}</p>
                    <div className="char-btn">
                        <button className="homepage"><a href={homepage}>Homepage</a></button>
                        <button className="wiki"><a href={wikipage}>Wiki</a></button>
                    </div>
                </div>
            </div>
        )
    }
    
    const errorMsg = error ? <Error/> : null;
    const spinner = loading ? <Spinner/> : null;
    const content = !(loading || error) && char !== null ? <HeroInfo char={char}/> : null;

    return (
        <section className="character">
            {errorMsg}
            {spinner}
            {content}
            <div className="character-random">
                <span className="random-text">Your today's char!<br/>Want to take a look at him?</span>
                <span className="random-text">Or you can choose another one:</span>
                <button className="homepage" onClick={updateChar}><a href="#">Try it</a></button>
            </div>
        </section>
    )
};

export default Character;

