import { useState, useEffect } from 'react';
import PropTypes from 'prop-types';

import Spinner from '../spinner/Spinner';
import Error from '../error/Error';
import useMarvelService from '../../services/MarvelService';

import './characterSidebar.scss';

const CharacterSidebar = (props) => {

    const [char, setChar] = useState(null);

    const {loading, error, getChar, clearError} = useMarvelService();

    useEffect(() => {
        
        updateChar()
    }, [props.charId])
    
    const updateChar = () => {
        const {charId} = props;

        if (!charId) {
            return;
        }
        clearError();
        getChar(charId)
            .then(onCharLoaded)
    }

    const onCharLoaded = (char) => {
        setChar(char);
    }

        let containerHeight = '';
        const empty = char || loading || error ? null : <EmptyInfo/>
        const errorMsg = error ? <Error/> : null;
        const spinner = loading ? <Spinner/> : null;
        const content = !(loading || error || !char) ? <CharInfo char={char} onScrollBack={props.onScrollBack}/> : null;
        char || loading || error ? containerHeight = '100%' : containerHeight = '300px'
        
        return (
            <section className="character-sidebar" style={{height: `${containerHeight}`}}>
                {empty}
                {errorMsg}
                {spinner}
                {content}
            </section>
        )
}

const EmptyInfo = () => {
    return (
        <div className='empty'>
            <span className="select__text">Please select a character from the list</span>
            <span className="first__line"></span>
            <span className="bold__line"></span>
            <span className="bold__line"></span>
            <span className="bold__line"></span>
        </div>
    )
}

const CharInfo = (props) => {
    const {name, thumbnail, desc, homepage, wikipage, comics} = props.char;
    const onScrollBack = props.onScrollBack;
    
    let subTitle = 'Comics:';
    if (comics.length < 1) subTitle = 'There\'s no comics for this character...';
                        
    return (
        <>
        <div className="character-block">
            <div className="character-block__img">
                {thumbnail.match(/not_available/g)
                ? <img src={thumbnail} alt={name} style={{ objectFit: 'contain' }} />
                : <img src={thumbnail} alt={name} />}
            </div>
            <div className="character-block__name">
                <h2 className="char-name">{name}</h2>
                <div className="char-btn">
                    <button className="homepage"><a href={homepage}>Homepage</a></button>
                    <button className="wiki"><a href={wikipage}>Wiki</a></button>
                </div>
            </div>
        </div>
            <p className="char-desc">{desc}</p>
            <div className="char-comics">
                <h3 className="char-comics__title">
                    {subTitle}</h3>
                <ul className="char-comics__list">
                    {
                    comics.map((item, i) => {    
                        // eslint-disable-next-line
                        if (i >= 10) return
                        return (
                            <li key={i} className="char-comics__list_item">
                                {item.name}
                            </li>
                        )
                    })
                }                    
                </ul>
                <button className='down__btn' onClick={onScrollBack}>Go back to hero select</button>
            </div>
        </>
    )
}

CharacterSidebar.propTypes = {
    charId: PropTypes.number
}

export default CharacterSidebar;

