import { useState, useRef } from 'react';
import Header from '../header/Header';

import Banner from '../banner/Banner';
import Character from '../character/Character';
import CharacterList from '../characterList/CharacterList';
import CharacterSidebar from '../characterSidebar/CharacterSidebar';
import ErrorBoundary from '../errorBoundary/ErrorBoundary';
import ComicsList from '../comicsList/ComicsList';
import ComicsItem from '../comicsItem/ComicsItem';
import CharacterForm from '../characterForm/CharacterForm';

import backgroundImg from '../../resources/img/bg_chars.svg';
import '../../style/style.scss'

const App = () => {
    
    const [selectedChar, setSelectedChar] = useState(null);
    const [currentElem, setCurrentPosition] = useState({});
    
    const onScrollBack = () => {
        currentElem.scrollIntoView({behavior: 'smooth'});
    }

    const onCharSelected = (id, e) => {
        setSelectedChar(id);
        setCurrentPosition(e.target);
        const sidebar = document.querySelector('.character-sidebar');
        const isMoreThanHalfVisible = window.scrollY > 400;
        if (isMoreThanHalfVisible) sidebar.scrollIntoView({ behavior: 'smooth' });
    }

    let bgImage = backgroundImg;
        return (
            <>
            <div className="bg__art"
                style={{
                        backgroundImage: `url("${bgImage}")`
                }}>
                <div className="container">
                    <Header/>
                    <Banner/>
                    <ErrorBoundary>
                        <Character/>
                    </ErrorBoundary>
                    <div className="main">
                        <ErrorBoundary>
                        <CharacterList onCharSelected={onCharSelected}/>
                        </ErrorBoundary>
                        <ErrorBoundary>
                        <CharacterSidebar charId={selectedChar} onScrollBack={onScrollBack}/>
                        </ErrorBoundary>

                        <ErrorBoundary>
                            <ComicsList/>
                        </ErrorBoundary>
                        {/* <ErrorBoundary>
                            <ComicsItem/>
                        </ErrorBoundary> */}
                    </div>
                </div>
            </div>
            </>
        )
    };

export default App;
