import './header.scss'

const Header = () => {
    return (
        <header className="header">
            <h1 className="logo">
                <span className="accent">Marvel</span> information portal
            </h1>
            <div className="links">
                <a className="links__link_active" href="#">Characters</a>/
                <a className="links__link" href="#">Comics</a>
            </div>
        </header>
    )
}

export default Header;