import img from './error.gif'

const Error = () => {
    return (
        // FOR USING PUBLIC FOLDER
        // <img src={process.env.PUBLIC_URL + '/error.gif'}/>

        <img src={img} alt="Error message" style={{width: '250px', height: '250px', objectFit: 'contain', margin: '0 auto'}}/>
    )
}

export default Error;